body {
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  position: relative;
}
body::before {
  content: '';
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  opacity: .6;
  z-index: -100;
  background: url("images/kosmonavt.jpg") no-repeat center;
  background-size: cover;
}